.my-drawer {
    position: relative;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar {
    background-color: #fff;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar .am-list {
    width: 300px;
    padding: 0;
}
.sub-title {
    color: #888;
    font-size: 14px;
    padding: 15px 0 9px 15px;
}
.not-square-grid .am-grid-icon {
    width: 40px;
    height: 60px;
}
.space-carousel {
    padding: 16px;
    background: #DEF1E5;
    overflow: hidden;
}